        content: '원칙적으로 비용, 이자, 원본의 순서로 충당하여야 하나, 당사자 사이에 합의가 있는 경우에는 위 순서와는 달리 충당의 순서를 인정할 수 있다. 변제자(채무자)와 변제수령자(채권자)는 변제로 소멸한 채무에 관한 보증인 등 이해관계 있는 제3자의 이익을 해하지 않는 이상 이미 급부를 마친 뒤에도 기존의 충당방법을 배제하고 제공된 급부를 어느 채무에 어떤 방법으로 다시 충당할 것인가를 약정할 수 있다. 법원에 제출되어 상대방에게 송달된 준비서면 등에 자백에 해당하는 내용이 기재되어 있는 경우라도 그것이 변론기일이나 변론준비기일에서 진술 또는 진술간주 되면 재판상 자백이 성립한다. 당사자가 변론에서 상대방이 주장하기 전에 스스로 자신에게 불이익한 사실을 진술하고 상대방이 이를 명시적으로 원용하거나 그 진술과 일치되는 진술을 하는 경우에도 재판상 자백이 성립된다.'
